import "./StepTable.scss";
import { Steps } from "antd";
import React from "react";
import FirstForm from "./components/FirstForm";
import SecondForm from "./components/SecondForm";
import ThirdForm from "./components/ThirdForm";
import BaseBreadcrumb from "../../../components/base-breadcrumb/BaseBreadcrumb";
const { Step } = Steps;

const steps = [
  {
    title: "First",
    content: FirstForm,
  },
  {
    title: "Second",
    content: SecondForm
  },
  {
    title: "Last",
    content: ThirdForm,
  },
];

const StepTable = () => {
  const [current, setCurrent] = React.useState(0);
  const [params, setParams] = React.useState({});
  const next = () => {
    setCurrent(current + 1);
  };

  const prev = () => {
    setCurrent(current - 1);
  };

  const reset = () => {
    setCurrent(0);
    setParams({});
  }

  const handleNext = (data) => {
    setParams({...params,...data});
    next();
  }
  const handlePrev = (data) => {
    prev();
  }
  const handleReset = () => {
    reset();
  }

  return (
    <>
      <div className="step-header-wrapper p-2 bg-white main-reverse">
        <BaseBreadcrumb />
        <h2 className="fs-lg fw-bolder m-y-1"></h2>
        <p></p>
      </div>
      <div className="step-content-wrapper bg-white">
        <Steps current={current}>
          {steps.map((item) => (
            <Step key={item.title} title={item.title} />
          ))}
        </Steps>
        <div className="step-content">{
          React.createElement(steps[current].content, {handleNext, handlePrev, handleReset, params})
        }</div>
      </div>
    </>
  );
};
export default StepTable;
